import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { LoggedInUser } from '../models/loggedInUser';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private router: Router) { }

  checkIfLoggedIn() {
    return localStorage.getItem('loggedInUserInfo') ? true : false
  }

  login(user: LoggedInUser) {
    localStorage.setItem('loggedInUserInfo', JSON.stringify(user));
    this.router.navigate(['/dashboard'])
  }

  logout() {
    localStorage.removeItem('loggedInUserInfo');
    this.router.navigate(['/login'])
  }

  getLoggedInUserInfo() {
    return localStorage.getItem('loggedInUserInfo') ? JSON.parse(localStorage.getItem('loggedInUserInfo')) : null;
  }

}
