import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { LoggedInUser } from 'src/app/models/loggedInUser';
import { AuthService } from 'src/app/services/auth.service';
import { AppConfig } from 'src/app/helper/app-config';
import { Router } from '@angular/router';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  @ViewChild('useraction', {static: false}) useraction: ElementRef;
  mainLogo = AppConfig.logo.toString();
  userLogo = AppConfig.userLogo.toString();
  userDrop = AppConfig.userDrop.toString();
  logoutIcon = AppConfig.logoutIcon.toString()

  userInfo: LoggedInUser;

  constructor(private router: Router, private authService: AuthService) { }

  ngOnInit() {
    this.userInfo = this.authService.getLoggedInUserInfo()
  }

  goToHomePage() {
    this.router.navigate(['/dashboard']);
  }
  
  logout() {
    this.authService.logout();
  }

  showAction() {
    this.useraction.nativeElement.classList.toggle("display");
  }
}
